.hidden-class {
  display: none !important;
}
.button-search {
  position: absolute !important;
  top: 20px;
  right: 32px;
  z-index: 999;
}
.hidden-tab .sentre-tabs-nav-wrap {
  display: none !important;
}
.banner-farming {
  position: relative;
  background-image: url(../../static/media/bg-banner.d673e1637700be6ec978.png) !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  background-position: center !important;
}
.banner-farming::after {
  content: '';
  position: absolute;
  width: 45%;
  height: 100%;
  background-image: url(../../static/media/bg-left-banner.fe042b77687f9320a57a.png);
  background-repeat: no-repeat;
  background-position: 90% bottom;
  background-size: 45%;
  bottom: 0;
  right: 0;
}
.banner-farming.banner-farming-mobile {
  padding-top: 145px !important;
}
.banner-farming.banner-farming-mobile::after {
  width: 100%;
  height: 145px;
  top: 0;
  right: 0 !important;
  background-size: contain;
  bottom: unset;
  background-position: top center;
}
.expand-farm-card {
  position: relative;
  top: -8px;
  background-color: #f4f4f5 !important;
  border: none !important;
  border-radius: 0 0 8px 8px !important;
  z-index: 0;
}
.expand-farm-card .sentre-collapse-header {
  padding: 0 !important;
  border: none !important;
}
.expand-farm-card .sentre-collapse-content-box {
  padding: 32px 16px 16px 16px !important;
}
.expand-farm-card *:not(button) {
  background-color: transparent !important;
}
.list-type-items {
  padding-left: 20px;
}
.list-type-items span {
  display: list-item;
  list-style-type: disc;
}
.custom-selector .sentre-select-selector {
  padding-left: 0 !important;
}
.custom-selector .sentre-select-selector .sentre-select-selection-item {
  line-height: normal !important;
}
.account-card {
  border-radius: 8px !important;
}

#dark #sen_farming .expand-farm-card {
  background-color: #212433 !important;
}
#dark #sen_farming .farming-card {
  background-color: #2c2e3d !important;
}
#dark #sen_farming .expand-farm-card, #dark #sen_farming .farm-card-widget {
  box-shadow: none !important;
  background: #2c2e3d !important;
}

#light #sen_farming .expand-card {
  background-color: #f4f4f5 !important;
}
#light #sen_farming .farming-card {
  background-color: #fff !important;
}
#light #sen_farming .account-card, #light #sen_farming .farm-card-widget {
  box-shadow: none !important;
  background: #fff !important;
}

